<template>
  <div>
    <vx-header :showcancel="1" @cancel='cancel' :title="`OSD`" :showOsdFont='1' @savesFont='savesFont'></vx-header>
    <!-- <img style="position: absolute; z-index: -1" :src="src" alt="" ref="img"> -->
    <div class="vx-container">
      <div class="layout-right">
        <div class="input-area">
          <!--  -->
          <div ref="divparent" contenteditable="true" class="input-area-text needsclick" :class="{bolds: b, italic: i, underline: u}" :style="{fontFamily: selectedFontFamily, fontSize: selectedFontSize + 'px', color: selectedcolor ,background: selectedBgColor}" @click="testText">
            {{$t('osd.wel')}}
          </div>
          <!--  -->
          <div ref='divChild' contenteditable="true" class="input-area-text2 needsclick" :class="{bolds: b, italic: i, underline: u}" :style="{fontFamily: selectedFontFamily, fontSize: selectedFontSize * 2 + 'px', color: selectedcolor}">
          </div>
        </div>

        <div class="golbal-font">
          <span @click="blod">
            <img v-if="!b" class="B" src="../../assets/common/B.png" alt="">
            <img v-else class="B" src="../../assets/common/Bsel.png" alt="">
          </span>
          <span @click="italics">
           <img v-if="!i" class="I" src="../../assets/common/I.png" alt="">
           <img v-else class="I" src="../../assets/common/Isel.png" alt="">
          </span>
          <span @click="underlines">
            <img v-if="!u" class="U" src="../../assets/common/U.png" alt="">
            <img v-else class="U" src="../../assets/common/Usel.png" alt="">
         </span>
        </div>
        <!--  -->
        <mt-popup v-model="showChangeStyle.showChangeFont" popup-transition="popup-fade" :modal="false" :closeOnClickModal='false'>
          <div class="change-font" v-show="showChangeStyle.showChangeFont">
            <div class="swiper-font">
              <div class="swiper-wrapper">
                <div class="swiper-slide" :class="{activeFont:selectFontId == item.id}" v-for='item in fonts' @click="changeFont(item)">
                  {{item.name}}
                </div>
              </div>
            </div>
          </div>
        </mt-popup>
        <!--  -->
        <mt-popup v-model="showChangeStyle.showChangeSize" popup-transition="popup-fade" :modal="false" :closeOnClickModal='false'>
          <div class="change-size" v-show="showChangeStyle.showChangeSize">
            <div class="change-size-title">
              <span>A</span>
              <span>{{ selectedFontSize }}</span>
            </div>
            <div class="change-size-content">
              <div class="ranges">
                <div :class="{vis: selectedFontSize === 9}"></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div :class="{vis: selectedFontSize === 72}"></div>
              </div>
              <mt-range v-model="rangeValue" :min="9" :max="72" :step="9" :bar-height="2">
              </mt-range>
            </div>
          </div>
        </mt-popup>
        <!--  -->
        <mt-popup v-model="showChangeStyle.showChangeColor" popup-transition="popup-fade" :modal="false" :closeOnClickModal='false'>
          <div class="change-color" v-show="showChangeStyle.showChangeColor">
            <div class="swiper-color">
              <div class="swiper-wrapper">
                <div class="swiper-slide" :class="{activeColor:selectColorId == items.id}" v-for='items in colors' @click="changeColor(items)" :style="{background: items.bgColors}">
                </div>
              </div>
            </div>
          </div>
        </mt-popup>
        <!--  -->
        <mt-popup v-model="showChangeStyle.showChangeBackground" popup-transition="popup-fade" :modal="false" :closeOnClickModal='false'>
          <div class="change-background" v-show="showChangeStyle.showChangeBackground">
            <div class="change-background-list" v-for="item in textBgColor" :style="{background: item.bgColors}" :class="{activeColors:selectedBgColor == item.bgColors}" @click="changeBgColor(item)"></div>
          </div>
        </mt-popup>
      </div>
      <div class="footer">
        <div :class="{active:showChangeStyle.showChangeFont}" @click="showChangeFonts">
          <img v-if="showChangeStyle.showChangeFont" src="../../assets/common/aczticon.png" alt="">
          <img v-else src="../../assets/common/zticon.png" alt="">
          <!-- <span>{{$t('osgFont.font')}}</span> -->
        </div>
        <div :class="{active:showChangeStyle.showChangeSize}" @click="showChangeSizes">
          <img v-if="showChangeStyle.showChangeSize" src="../../assets/common/aczhicon.png" alt="">
          <img v-else src="../../assets/common/zhicon.png" alt="">
          <!-- <span>{{$t('osgFont.fontSize')}}</span> -->
        </div>
        <div :class="{active:showChangeStyle.showChangeColor}" @click="showChangeColors">
          <img v-if="showChangeStyle.showChangeColor" src="../../assets/common/acysicon.png" alt="">
          <img v-else src="../../assets/common/ysicon.png" alt="">
          <!-- <span>{{$t('osgFont.color')}}</span> -->
        </div>
        <div :class="{active:showChangeStyle.showChangeBackground}" @click="showChangeBackgrounds">
          <img v-if="showChangeStyle.showChangeBackground" src="../../assets/common/acbjicon.png" alt="">
          <img v-else src="../../assets/common/bjicon.png" alt="">
          <!-- <span>{{$t('osgFont.background')}}</span> -->
        </div>
      </div>
    </div>

    <vx-conform v-if="showTip" @cancelConform="cancelConform" @sureConform="sureConform" :message="$t('osgFont.tipMessage')"></vx-conform>
    <vx-indicator v-if="showIndicatoe" :time="time" :text="progress" @progressCancel="progressCancel"></vx-indicator>
  </div>
</template>

<script>
  import Swiper from 'swiper';
  import html2canvas from 'html2canvas';
  import { browser, dataURLtoFile, setLoc, getLoc, setSen, getSen, Confirm } from '../../utils';
  import { mapGetters, mapMutations } from 'vuex';
  import htp from "../../api/http";
  import urls from '../../api/apiurl';
  import { Popup } from 'mint-ui';
  import { Toast } from 'mint-ui';

  export default {
    // beforeRouteEnter(from, to, next) {
    //   if(getSen('isHScreen')) {
    //     next({ name: 'osdtext-h' });
    //   }
    //   next();
    // },
    created() {
      // alert('');
      //
      this.formatResolution();
    },
    mounted() {
      this.initData();
      this.showChangeFonts();
      // console.log(width, this.box.height);
      // setTimeout(() => {
      //   if(this.box.width >= 1920) {
      //     this.$refs['divChild'].style.width = 1920 + 'px';
      //   } else {
      //     this.$refs['divChild'].style.width = this.box.width + 'px';
      //   }
      // }, 500);
    },
    data() {
      return {
        stopGetPro: false,
        time: '',
        showIndicatoe: false,
        textId: '',
        box: {
          width: '',
          height: '',
          rate: ''
        },
        imgage: {
          width: '',
          height: ''
        },
        src: '',
        imageFile: '',
        inputMessage: '', //
        showTip: false, //
        changed: false, //
        b: false,
        i: false,
        u: false,
        showChangeStyle: {
          showChangeFont: false,
          showChangeSize: false,
          showChangeColor: false,
          showChangeBackground: false
        },
        rangeValue: 36,
        selectedFontSize: 18,
        selectedFontFamily: 'Arial',
        selectFontId: 0,
        fonts: [
          {
            id: 0,
            name: 'Arial',
            family: 'Arial'
          },
          {
            id: 1,
            name: 'Calibri',
            family: 'Calibri'
          },
          {
            id: 2,
            name: '',
            family: 'KaiTi'
          },
          {
            id: 3,
            name: '',
            family: 'MicrosoftYaHei'
          },
          {
            id: 4,
            name: '',
            family: 'SongTi'
          },
          {
            id: 5,
            name: 'Wingdings2',
            family: 'Wingdings2'
          }
        ],
        selectedcolor: '#ff0000',
        selectColorId: 3,
        colors: [
          {
            id: 0,
            bgColors: '#000000'
          },
          {
            id: 1,
            bgColors: '#ffffff'
          },
          {
            id: 2,
            bgColors: '#980000'
          },
          {
            id: 3,
            bgColors: '#ff0000'
          },
          {
            id: 4,
            bgColors: '#ff9900'
          },
          {
            id: 5,
            bgColors: '#ffff00'
          },
          {
            id: 6,
            bgColors: '#00ff00'
          },
          {
            id: 7,
            bgColors: '#00ffff'
          },
          {
            id: 8,
            bgColors: '#4a86e8'
          },
          {
            id: 9,
            bgColors: '#0000ff'
          },
          {
            id: 10,
            bgColors: '#9900ff'
          },
          {
            id: 11,
            bgColors: '#ff00ff'
          },
          {
            id: 12,
            bgColors: '#60fd30'
          },
          {
            id: 13,
            bgColors: '#e0d9e3'
          },
          {
            id: 14,
            bgColors: '#b9eeec'
          },
          {
            id: 15,
            bgColors: '#07bee4'
          },
          {
            id: 16,
            bgColors: '#653565'
          },
          {
            id: 17,
            bgColors: '#465d43'
          }
        ],
        selectedBgColor: '#ffffff',
        textBgColor: [
          {
            bgColors: '#ffffff'
          },
          {
            bgColors: '#b9eeec'
          },
          {
            bgColors: '#e0d9e3'
          },
          {
            bgColors: '#000000'
          }
        ],
        progress: 0,
        textarea: this.$t('osd.wel')
      };
    },
    methods: {
      //
      formatResolution() {
        htp.get(urls.getOutputResolutionRatio)
          .then(res => {
            if(res.status === 10000000) {
              this.box.width = res.data.width;
              this.box.height = res.data.height;
              this.box.rate = res.data.rate;
            } else {
              Toast(this.$t('common.requestFailed'));
            }
          });
      },
      initData() {
        if(this.$route.params.id !== undefined) {
          this.textId = this.$route.params.id;
          htp.post(urls.getOsdTxtEditInfo, { id: this.textId }).then(res => {
            if(res.status === 10000000) {
              console.log(res);
              this.$refs['divparent'].innerText = res.data.text.replace(/()/g, '\n');
              this.selectedFontSize = parseInt(res.data.fontSize);
              this.selectedFontFamily = res.data.fontFamily;
              this.selectedcolor = res.data.color;
              this.selectedBgColor = res.data.bgColors;
              this.b = res.data.fontWeight;
              this.i = res.data.fontStyle;
              this.u = res.data.textDecoration;
              this.textarea = res.data.text.replace(/()/g, '\n');
              this.colors.forEach(item => {
                if(item.bgColors === res.data.color) {
                  this.selectColorId = item.id;
                }
              });
              this.fonts.forEach(item => {
                if(item.family === res.data.fontFamily) {
                  this.selectFontId = item.id;
                }
              });
              this.initRange(parseInt(res.data.fontSize));
            }
          });
        } else {
          return;
        }
      },
      //
      showChangeFonts() {
        this.showChangeStyle.showChangeSize = false;
        this.showChangeStyle.showChangeColor = false;
        this.showChangeStyle.showChangeBackground = false;
        this.showChangeStyle.showChangeFont = !this.showChangeStyle.showChangeFont;
        setTimeout(() => {
          this.$nextTick(() => {
            var swiper = new Swiper('.swiper-font', {
              slidesPerView: 'auto',
              spaceBetween: 5
            });
          });
        }, 100);
      },
      changeFont(item) {
        this.selectedFontFamily = item.family;
        this.selectFontId = item.id;
        console.log('');
        this.changed = true;
      },
      //
      showChangeSizes() {
        this.showChangeStyle.showChangeFont = false;
        this.showChangeStyle.showChangeColor = false;
        this.showChangeStyle.showChangeBackground = false;
        this.showChangeStyle.showChangeSize = !this.showChangeStyle.showChangeSize;
      },
      //
      showChangeColors() {

        this.showChangeStyle.showChangeFont = false;
        this.showChangeStyle.showChangeSize = false;

        this.showChangeStyle.showChangeBackground = false;
        this.showChangeStyle.showChangeColor = !this.showChangeStyle.showChangeColor;
        setTimeout(() => {
          this.$nextTick(() => {
            var swiper = new Swiper('.swiper-color', {
              slidesPerView: 'auto',
              spaceBetween: 15
            });
          });
        }, 100);
      },
      changeColor(items) {
        this.selectedcolor = items.bgColors;
        this.selectColorId = items.id;
        console.log('');
        this.changed = true;
      },
      //
      showChangeBackgrounds() {
        this.showChangeStyle.showChangeFont = false;
        this.showChangeStyle.showChangeSize = false;
        this.showChangeStyle.showChangeColor = false;
        this.showChangeStyle.showChangeBackground = !this.showChangeStyle.showChangeBackground;
      },
      changeBgColor(item) {
        this.selectedBgColor = item.bgColors;
        console.log('');
        this.changed = true;
      },
      //
      blod() {
        this.b = !this.b;
        this.changed = true;
      },
      //
      italics() {
        this.i = !this.i;
        this.changed = true;
      },
      //
      underlines() {
        this.u = !this.u;
        this.changed = true;
      },
      cancel() {
        let text = this.$refs['divparent'].innerText;
        if(this.textarea == text && !this.changed) {
          this.$router.go(-1);
        } else {
          this.showTip = true;
        }
      },
      //
      random(lower, upper) {
        return Math.floor(Math.random() * (upper - lower)) + lower;
      },
      savesFont() {
        this.stopGetPro = false; //
        console.log('');
        let $divparent = this.$refs['divparent'];
        let $divChild = this.$refs['divChild'];
        $divChild.style.display = 'block';
        $divChild.innerText = $divparent.innerText;
        //
        let opt = {
          backgroundColor: '#383940',
          scale: 0.5
        };
        this.$nextTick(() => {
          html2canvas(document.querySelector(".input-area-text2"), opt).then((canvas) => {
            //canvas

            if(parseInt(canvas.style.height) * 0.5 <= 1080) {
              let dataUrlBase = canvas.toDataURL("image/png");
              this.src = dataUrlBase;
              this.imageFile = dataURLtoFile(dataUrlBase, 'vx' + this.random(100, 1) + '.png');
              let formData = new FormData();
              let jsonData = {
                fontFamily: this.selectedFontFamily, //
                fontSize: this.selectedFontSize + 'px', //
                bgColors: this.selectedBgColor ? this.selectedBgColor : '#ffffff', //
                color: this.selectedcolor ? this.selectedcolor : '#ff0000', //
                text: $divChild.innerText, //
                id: this.textId !== '' ? this.textId : -1, //textIdid-1
                type: 1, //type1
                fontWeight: this.b, //  bloon
                fontStyle: this.i, // bloon
                textDecoration: this.u //  bloon
              };

              formData.append('vx' + this.random(100, 1) + '.png', this.imageFile);
              let reg = /^[a-zA-Z0-9_\u4e00-\u9fa5]/g;
              if(reg.test(jsonData.text.trim())) {
                console.log('');
                htp.post(urls.setOsdEditInfo, jsonData).then(res => {
                  if(res.status === 10000000) {
                    this.showIndicatoe = true;
                    this.imgage.width = canvas.style.width;
                    this.imgage.height = canvas.style.height;
                    setTimeout(() => {
                      this.getProgress();
                    }, 500);
                    htp.upload(urls.uploadOSD).then(res => {
                      if(res.status === 10000000) {
                        console.log('');
                        Toast(this.$t('osd.uploadSuccess'));
                      } else if(res.errorCode[0] === 20070001) {
                        // Toast(this.$t('osd.cancelSuccess'));
                      } else if(res.errorCode[0] === 20070002) {
                        Toast(this.$t('osd.OSDBusy'));
                      } else {
                        Toast(this.$t('osdFont.notext'));
                        console.log('');
                      }
                    });
                  }
                });
              } else {
                console.log('');
                Toast(this.$t('osgFont.notext'));
              }



            } else {
              Toast(this.$t('osgFont.overSize'));
            }
            $divChild.style.display = 'none';

          });
        });
      },
      getProgress() {
        htp.post(urls.getProgress, { control: 1 }).then(res => {
          if(res.status === 10000000) {
            if(this.stopGetPro) {
              console.log('');
              return false;
            }
            this.progress = res.data.progress;
            console.log('progress', this.progress);
            this.time = parseInt((parseInt(this.imgage.width) * parseInt(this.imgage.height)) / 2371 * ((100 - this.progress) / 100));
            console.log('time' + this.time);
            if(this.progress !== -1 && this.progress !== 100) {
              this.getProgress();
            } else if(this.progress === 100) {
              console.log('');
              this.showIndicatoe = false;
              Toast(this.$t('osd.uploadSuccess'));
              setTimeout(() => {
                this.$router.go(-1);
              }, 300);
            } else {
              this.showIndicatoe = false;
              this.time = 0;
            }
          }
        });
      },
      progressCancel() {
        Confirm(this.$t('osd.cancelOSD')).then(active => {
          console.log('');
          htp.post(urls.getProgress, { control: 0 }).then(res => {
            if(res.status === 10000000) {
              this.showIndicatoe = false;
              this.progress = 0;
              this.stopGetPro = true;
              console.log(1111111111111111, res.data.progress);
            }
          });
        });
      },
      cancelConform() {
        this.showTip = false;
      },
      sureConform() {
        console.log('');
        this.showTip = false;

        this.$router.go(-1);

      },
      getStyle(element) {
        return window.getComputedStyle(element, null);
      },
      testText() {
        let $divparent = this.$refs['divparent'];
        if($divparent.innerText === this.$t('osd.wel')) {
          if(this.$route.params.id === undefined) {
            $divparent.innerText = '';
            this.textarea = '';
            this.changed = false;
          }
        }
      },
      initRange(val) {
        switch(val) {
          case 9:
            this.rangeValue = 9;
            break;
          case 11:
            this.rangeValue = 18;
            break;
          case 14:
            this.rangeValue = 27;
            break;
          case 18:
            this.rangeValue = 36;
            break;
          case 22:
            this.rangeValue = 45;
            break;
          case 26:
            this.rangeValue = 54;
            break;
          case 36:
            this.rangeValue = 63;
            break;
          case 72:
            this.rangeValue = 72;
            break;
          default:
            this.rangeValue = 9;
            break;
        }
      }
    },
    computed: {
      ...mapGetters(['getIsHScreen'])
    },
    watch: {
      rangeValue(val, oldVal) {
        this.changed = true;
        switch(val) {
          case 9:
            this.selectedFontSize = 9;
            break;
          case 18:
            this.selectedFontSize = 11;
            break;
          case 27:
            this.selectedFontSize = 14;
            break;
          case 36:
            this.selectedFontSize = 18;
            break;
          case 45:
            this.selectedFontSize = 22;
            break;
          case 54:
            this.selectedFontSize = 26;
            break;
          case 63:
            this.selectedFontSize = 36;
            break;
          case 72:
            this.selectedFontSize = 72;
            break;
          default:
            this.selectedFontSize = 9;
            break;
        }
      }
    }
  };
</script>

<style lang="less" scoped>
  .vis {
    visibility: hidden;
  }
  .bolds {
    font-weight: bold;
  }
  .italic {
    font-style: italic;
  }
  .underline {
    text-decoration: underline;
  }
  .activeFont {
    color: #f56800 !important;
  }
  .activeColor {
    outline: 2px solid #f56800 !important; /*no*/
    outline-offset: 1px; /*no*/
  }
  .input-area-text {
    background: #fff;
  }
  .activeColors {
    border: 2px solid #f56800 !important; /*no*/
  }

  //
  @media screen and (orientation: portrait) {
    .mint-popup {
      width: 100%;
      height: 10%;
      top: 86.5%;
      position: absolute;
    }
    .vx-container {
      padding-bottom: 15%;
    }
    .layout-right {
      height: 100%;
    }
    .input-area {
      width: 100%;
      height: 79%;
      position: relative;
      .input-area-text {
        width: 100%;
        height: 100%;
        padding: 10px;
        font-size: 9px; /*no*/
        -webkit-user-select: text;
        border: none;
        outline: none;
        border-bottom: 1px solid #616574;
        text-align: left;
        overflow: auto;
        position: absolute;
        top: 0;
        left: 0;
      }
      .input-area-text2 {
        font-size: 9px; /*no*/
        color: #dddddd;
        text-align: left;
        overflow: auto;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        display: none;
        -webkit-user-select: text;
        min-width: 128px; /*no*/
        min-height: 128px; /*no*/
        white-space: nowrap;
      }
    }
    .golbal-font {
      color: #ddd;
      text-align: left;
      // padding: 40px 35px;
      background: #383940;
      height: 10%;
      width: 100%;
      display: flex;
      span {
        width: 17%;
        height: 100%;
        // display: block;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      .B {
        display: block;
        width: 34px;
        height: auto;
      }
      .I {
        width: 16px;
        height: 40px;
      }
      .U {
        width: 33px;
        height: auto;
      }
    }
    .change-font,
    .change-size,
    .change-color,
    .change-background {
      width: 100%;
      height: 100%;
      padding: 0 10px;
    }
    .change-size {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      padding: 0 20px;
      color: #dddddd;
      font-size: 18px; /*no*/
      .change-size-title {
        width: 100%;
        padding: 0 10px;
        display: flex;
        justify-content: space-between;
        font-size: 14px; /*no*/
      }
      .change-size-content {
        width: 100%;
        position: relative;
        .ranges {
          width: 100%;
          height: 20px;
          display: flex;
          justify-content: space-between;
          position: absolute;
          top: 10px;
          > div {
            width: 1px; /*no*/
            background: #ddd;
          }
        }
      }
    }
    .change-background {
      display: flex;
      align-items: center;
      justify-content: space-around;
      .change-background-list {
        width: 88px;
        height: 88px;
        border: 2px solid #ccc;
        border-radius: 50%;
      }
    }
    .footer {
      position: fixed;
      bottom: 0;
      width: 100%;
      height: 8%;
      background: #fff;
      display: flex;
      justify-content: space-around;
      align-items: center;
      z-index: 999;
      background: #242428;
      color: #ddd;
      font-size: 32px;
      border-top: 1px solid rgba(107, 112, 130, 0.8); /*no*/
      div {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 25%;
        text-align: center;
        img {
          display: block;
          width: 45px;
          height: 40px;
          // margin-top: 8px;
          margin-bottom: 8px;
        }
      }
      // .active {
      //   color: #f56800;
      // }
    }

    /*swiper  */
    .swiper-font {
      width: 90%;
      margin: auto;
      height: 100%;
      position: relative;
      overflow-x: hidden;
      .swiper-wrapper {
        position: relative;
        width: 100%;
        height: 100%;
        z-index: 1;
        display: flex;
        box-sizing: content-box;

        .swiper-slide {
          font-size: 14px; /*no*/
          color: #ddd;
          flex-shrink: 0;
          position: relative;
          height: 100%;
          display: flex;
          align-items: center;
          padding: 0 12px;
          background: #212124;
          border: none;
        }
      }
    }
    /*swiper  */
    .swiper-color {
      height: 100%;
      position: relative;
      overflow-x: hidden;
      .swiper-wrapper {
        position: relative;
        width: 100%;
        height: 100%;
        z-index: 1;
        display: flex;
        align-items: center;
        box-sizing: content-box;
        .swiper-slide {
          font-size: 18px; /*no*/
          color: #ddd;
          flex-shrink: 0;
          position: relative;
          width: 88px;
          height: 88px;
          display: flex;
          align-items: center;
          border: 1px solid #fff;
        }
      }
    }
  }
  //
  @media screen and (orientation: landscape) {
    .mint-popup {
      width: 100%;
      height: 17%;
      // left: 55%;
      // right: 0;
      top: 92%;
      position: absolute;
    }
    .vx-container {
      display: flex;
      flex-direction: row-reverse;
    }
    .layout-right {
      width: 90%;
      position: relative;
      display: flex;
    }
    .input-area {
      width: 90%;
      height: 100%;
      // float: left;
      max-width: 90%;
      .input-area-text {
        width: 100%;
        height: 100%;
        padding: 10px;
        resize: none;
        font-size: 9px; /*no*/
        border: none;
        outline: none;
        overflow: scroll;
        -webkit-user-select: text;
        text-align: left;
      }
      .input-area-text2 {
        // padding: 10px;
        // resize: none;
        font-size: 9px; /*no*/
        color: #dddddd;
        text-align: left;
        overflow: auto;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        display: none;
        -webkit-user-select: text;
        min-width: 128px; /*no*/
        min-height: 128px; /*no*/
        white-space: nowrap;
      }
    }
    .golbal-font {
      // float: left;
      color: #ddd;
      text-align: right;
      padding: 20px 34.5px;
      background: #383940;
      display: flex;
      flex-direction: column;
      align-items: center;
      border-left: 1px solid #616574;
      height: 100%;
      width: 10%;
      span {
        margin-top: 93px;
      }

      .B {
        width: 34px;
        height: auto;
      }
      .I {
        width: 16px;
        height: 40px;
      }
      .U {
        width: 33px;
        height: auto;
      }
    }

    //
    .swiper-font {
      width: 90%;
      height: 100%;
      // float: left;
      position: relative;
      .swiper-wrapper {
        position: relative;
        width: 100%;
        height: 100%;
        z-index: 1;
        display: flex;
        box-sizing: content-box;
        .swiper-slide {
          font-size: 14px; /*no*/
          color: #ddd;
          flex-shrink: 0;
          position: relative;
          height: 100%;
          display: flex;
          align-items: center;
          padding: 0 12px;
          background: #212124;
          border: none;
        }
      }
    }
    //
    .change-size {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      padding: 0 10px;
      color: #dddddd;
      font-size: 14px; /*no*/
      .change-size-title {
        width: 100%;
        padding: 0 20px;
        display: flex;
        justify-content: space-between;
      }
      .change-size-content {
        width: 100%;
        position: relative;
        // padding: 0 20px;
        .ranges {
          width: 100%;
          height: 20px;
          display: flex;
          justify-content: space-between;
          position: absolute;
          top: 10px;
          > div {
            width: 1px; /*no*/
            background: #ddd;
          }
        }
      }
    }
    .change-background {
      display: flex;
      align-items: center;
      justify-content: space-around;
      float: left;
      width: 100%;
      .change-background-list {
        width: 77px;
        height: 77px;
        border: 2px solid #ccc;
        border-radius: 50%;
      }
    }
    .change-color {
      float: left;
    }
    .swiper-color {
      position: relative;
      overflow-x: hidden;
      height: 100%;
      .swiper-wrapper {
        position: relative;
        width: 100%;
        height: 100%;
        z-index: 1;
        display: flex;
        align-items: center;
        box-sizing: content-box;
        .swiper-slide {
          font-size: 18px;
          color: #ddd;
          flex-shrink: 0;
          position: relative;
          width: 66px;
          height: 66px;
          display: flex;
          align-items: center;
          border: 1px solid #ddd;
        }
      }
    }
    .footer {
      width: 10%;
      height: 100%;
      background: #fff;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      z-index: 999;
      background: #242428;
      color: #ddd;
      font-size: 32px;
      padding: 28px;
      div {
        // margin-bottom: 130px;
        text-align: center;
        img {
          width: 50px;
          height: auto;
        }
      }
      .active {
        color: #f56800;
      }
    }
  }
  .change-font,
  .change-size,
  .change-color,
  .change-background {
    width: 100%;
    height: 100%;
    padding: 0 10px;
  }
</style>


